export default {
	onShow() {
		this.getData();
	},
	onLoad() {},
	methods: {
		getData() {
			uni.post("/api/workflow/Pending", {}, msg => {
				this.todoList = msg.data;
			})
		},
		toExecute(item) {
			item.StepId = undefined;
			if (item.WorkflowName === "按团队申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './roomFlowsCtrl'
						})
					}
				})
			} else if (item.WorkflowName === "按机位申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './seatFlowsCtrl'
						})
					}
				})
			}
		}
	},
	data() {
		return {
			todoList: [],
		}
	}
}
